.order{
  
    width: 85%;
    margin: auto;
}

.order h3{
    text-align: center;
    padding-top: 20px;
}

.orderForm{
   
    width: 85%;
    margin: auto;

    
   
  
}

.orderForm form{ 
    width: 85%;
    margin: auto;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 20px;

    text-align: center;
    border: 1px solid rgb(148, 148, 148);
    display: flex;
    flex-direction: column;
    padding: 35px 0;
  
  
    
}

.orderForm form input{ 
    padding: 5px 7px;
    width: 30%;
    margin: 15px auto;
    border: 1px solid rgb(171, 168, 168);
    

}

.orderForm form select{
    padding: 5px 7px;
    width: 30%;
    margin: 15px auto;
    border: 1px solid rgb(171, 168, 168);
}


.orderForm form  button{
    padding: 10px 15px;
    font-size: 1.2em;
    border-radius: 20px;
    border: none;
    outline:none;
    /* border-color: green;  */
    background-color: rgb(46, 156, 196);
    color: white;  
    margin-left: 50%;
    transform:translate(-50%, -50%);
    margin-top: 50px;
    cursor: pointer;
    
}

@media screen and (max-width: 850px){

    .orderForm form{ 
        width: 100%;
        
        height: 40vh;
      
      
        
    }

  

    .orderForm form input{ 
        padding: 5px 7px;
        width: 85%;
       
        
    
    }
    

   

}